*/
.input,
.select,
.input-icon {
  appearance: none;
  background-color: #fff;
  border: solid 1px #cecece;
  border-radius: 0.3rem;
  padding: 0.65em;
  transition: border 0.3s, box-shadow 0.3s;
  display: inline-block;
  vertical-align: middle; }

.input:not(textarea),
.select:not([multiple]) {
  height: 2.75em; }

.input:focus,
.select:focus,
:focus + .input-icon {
  border-color: #b5b5b5;
  box-shadow: 0 0 1px #b5b5b5;
  outline: none; }

:-ms-input-placeholder {
  color: #b5b5b5; }

::-webkit-input-placeholder {
  color: #b5b5b5; }

/*
-------------------------------------------------------
2. Custom Checkbox/Radio
-------------------------------------------------------
*/
.input-radio input,
.input-checkbox input {
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
  position: absolute;
  clip: rect(0, 0, 0, 0); }

.input-icon {
  width: 1.5em;
  height: 1.5em;
  margin-top: -0.1em;
  background-position: center;
  background-repeat: no-repeat; }
  .input-radio .input-icon {
    border-radius: 50%; }
  .input-checkbox :checked + .input-icon {
    background-image: url("../../dist/svg/icon-check.svg");
    background-size: 60%; }
  .input-radio :checked + .input-icon {
    z-index: -1;
    position: relative; }
    .input-radio :checked + .input-icon::after {
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      content: '';
      position: absolute;
      border-radius: 50%;
      border: 0.45em solid #fff;
      background-color: #222;
      display: inline-block;
      vertical-align: middle; }

/*
-------------------------------------------------------
4. Custom Select
-------------------------------------------------------
*/
.select:not([multiple]) {
  background-image: url("../../dist/svg/icon-arrow-alt.svg");
  background-position: right 0.6em center;
  background-repeat: no-repeat;
  background-size: 0.85em 1em;
  padding-right: 2.4em; }

/*
-------------------------------------------------------
3. Disabled Elements
